.post {
  padding: 20px;
  background-color: #FFFFFF;
  border: 1px solid #cecece;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.15);
  transition: 0.3s linear;
}

.post__title {
  text-align: center;
  margin-bottom: 10px;
}

.post__historical-information {
  text-align: center;
  margin-bottom: 20px;
}

.post__link-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: -10px;
  margin-bottom: 15px;
}

.post__link-place {
  font-size: 13px;
  font-weight: bold;
  color: @lighten;
  text-align: center;
  margin-left: 15px;
  position: relative;

  &:hover,
  &:active {
    color: @darken;
    text-decoration: underline;
  }

  &:after {
    content: "";
    position: absolute;
    height: 10px;
    width: 2px;
    background-color: @lighten;
    left: -10px;
    top: 7.5px;
  }

  &:first-child {
    margin-left: 0;

    &:after {
      content: none;
    }
  }
}

.post__img-block {
  overflow: hidden;
  outline: 1px solid #cecece;
  border: 2px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.post__img {
  background: radial-gradient(50% 50% at 50% 50%, rgba(31, 31, 31, 0) 0%, rgba(31, 31, 31, 0.5) 100%);
  display: block;
  width: 100%;
}

.post__img-caption {
  background-color: @darken;
  color: @lightest;
  padding: 3px;
  text-align: center;
  line-height: 17px;
  font-size: 13px;
}

.post__list-icon {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}

.post__item-icon {
  margin-right: 10%;
  min-width: 5%;

  &:last-child {
    margin-right: 0;
  }
}

.post__link-icon {
  font-size: 0;

  &:hover .post__icon--active,
  &:active .post__icon--active {
    transform: scale(1.2);
  }
}

.post__icon {
  width: 100%;
  height: 100%;
  color: @border-primary;

  &--active {
    color: @default;
  }
}

.post__text {
  // color: @darkest;
  margin-bottom: 20px;

  &--br {
    text-indent: 10px;
  }
}
.post__block {
  margin-bottom: 40px;
}

.post__anchor {
  display: block;
  height: 50vh;
  margin-top: -50vh;
  visibility: hidden
}

.post__list-block {
  list-style: none;
}

.post__strong-block {
  margin-right: 10px;
}

.post__link-text {
  color: @lighten;
  text-align: center;
  font-size: 13px;
  font-weight: bold;
  transition: all 0.5s;
  text-decoration-line: overline;
  border-bottom: 1px solid @lighten;

  &:hover,
  &:active {
    color: @accent-color;
    border-color: @accent-color;
  }
}

@media @tablet {
  .post__img-caption {
    font-size: 15px;
    line-height: 24px;
  }
}
