.gallery {
  display: none;
}

@media @tablet-large {
  .gallery {
    display: block;
    padding: 30px 20px;
  }

  .gallery__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }

  .gallery__item {
    display: flex;
    height: auto;
    padding: 2% 2%;
  }

  .gallery__link {
    display: block;
    height: auto;
    width: 100%;
  }

  .gallery__img {
    display: block;
    background: radial-gradient(50% 50% at 50% 50%, rgba(31, 31, 31, 0) 0%, rgba(31, 31, 31, 0.5) 100%);
    width: 59px;
    min-width: 59px;
    height: 59px;
    object-fit: cover;
    cursor: zoom-in;
  }
}

@media @desktop {
  .gallery {
    width: 30%;
  }
}

//---------------------------

.gallery-zoom {
  width: 98%;
  max-width: 1600px;
  height: auto;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
  cursor: zoom-out;
  outline: 2px solid #cecece;
  border: 2px solid @white;
}

.gallery-overlay {
  position: absolute;
  display: block;
  width: 100vw;
  height: 100%;
  top: 0;
  left: 0;
  background-color: @modal-overlay-bg;
  z-index: 55;
}
