.map {
  width: 100%;
}

.map__interactive {
  background: radial-gradient(50% 50% at 50% 50%, rgba(31, 31, 31, 0) 0%, rgba(31, 31, 31, 0.5) 100%);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 800px 380px;
  max-width: 100%;
  height: 380px;

  &--mangup {
    background-image: url(../img/map-mangup-mobile.jpg);
  }
}

@media @tablet {
  .map__interactive {
    background-repeat: repeat;
    background-size: cover;
    outline: 1px solid #cecece;
    border: 2px solid rgba(0, 0, 0, 0.1);

    &--mangup {
    background-image: url(../img/map-mangup-desktop.jpg);
    }
  }
}
