.main-nav {
  margin: 0;
  padding: 0;
}

.main-nav__list {
  list-style: none;
  margin-top: 20px;
}

.main-nav__item {
  border-bottom: 1px solid @border-primary;
  position: relative;
  background-image: linear-gradient(to right, @bg-body 20%, @white 100%);

  &:first-child {
    border-top: 1px solid @border-primary;
  }

  &:hover > .main-nav__link {
    color: @lighten;
  }
}

.main-nav__link {
  text-decoration: none;
  color: @default;
  line-height: 20px;
  font-weight: bold;
  display: block;
  padding: 16px 33px;

  &:active[href] {
    color: @lightest;
    background-color: @darken;
  }

  &--list {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      border-bottom: 2px solid @border-primary;
      border-right: 2px solid @border-primary;
      display: block;
      width: 14px;
      height: 14px;
      right: 20px;
      transform: rotate(-45deg);
      transition: all 0.3s linear;
    }
  }
}

@media @tablet {
  .main-nav {
    margin: 0 20px 20px;
    background-color: @darken;
    max-width: 1110px;
    transition: all 0.2s ease;
  }

  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 0;
    box-shadow: 0px 3px 4px 0 rgba(0, 0, 0, 0.65);
  }

  .main-nav__item {
    border: none;
    min-width: 15%;

    &:first-child {
      border: none;
    }

    &::after {
      content: "";
      position: absolute;
      height: 20px;
      width: 1px;
      background-color: @default;
      box-shadow: 1px 0 1px 0 @darkest;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      z-index: 1;
    }
  }

  .main-nav__link {
    background-color: @darken;
    font-size: 13px;
    color: @color-lighten;
    padding: 5px 18px;

    &:hover[href] {
      color: @lighten;
  }

    &:active[href] {
      color: @lightest;
    }

    &--list {
      &::before {
        border: none;
        content: "\25BC";
        transform: rotate(0deg);
        right: 0;
        font-size: 10px;
      }
    }
  }
}

@media @tablet-large {
  .main-nav__item {
    min-width: 40px;
    flex-grow: 1;

    &::after {
      height: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      height: 10px;
      width: 100%;
      background-color: rgba(31, 32, 32, 0.3);
      left: 0;
      bottom: 0;
      border-top: 1px solid @default;
      z-index: 2;
    }
  }

  .main-nav__link {
    padding: 18px 0;
    text-align: center;

    &--list {
      padding: 18px 10px;
    }
  }
}

@media @desktop {
  .main-nav__item:hover .submenu {
    display: block;
  }
}

//----------------------------

.main-nav--fixed {
  width: 96.2%;
  position: fixed;
  z-index: 51;
  transition: all 0.5s ease;
  top: -100px;
  transform: translateY(100px);
}

@media @tablet {
  .main-nav--fixed {
    width: 95.2%;
  }
}

@media (min-width: 868px) {
  .main-nav--fixed {
    width: 95.6%;
  }
}

@media @tablet-large {
  .main-nav--fixed {
    width: 96.2%;
  }
}

//-----------------------

.main-nav__link-arrow::before {
  transform: rotate(45deg);
  transition: all 0.3s ease;
}

@media @tablet {
  .main-nav__link-arrow::before {
    transform: rotate(0);
  }
}
