@media @tablet-large {
  .recommendation {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      left: -20px;
      top: 100%;
      width: calc(100% + 40px);
      height: 1px;
      background-color: @border-primary;
    }
  }

  .recommendation__item {
    display: flex;
    width: 100%;
    max-width: 100%;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid @header-border;

    &:last-child {
      border-bottom: none;
    }
  }

  .recommendation__link {
    line-height: 16px;
    color: @grey;
    font-size: @font-size-base;
    display: flex;

    &:hover,
    &:active {
      color: @accent-color;
    }
  }

  .recommendation__image {
    margin-right: 10px;
    min-width: 55px;
    height: 55px;
    outline: 1px solid #cecece;
    border: 2px solid rgba(0, 0, 0, 0.1);
  }

  .recommendation__text {
    width: 74%;
    font-weight: bold;
  }
}
