// Рекламный блок
.news-aside {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  position: relative;
  display: none;

  &::after {
    position: absolute;
    content: "";
    left: -20px;
    top: 100%;
    width: calc(100% + 40px);
    height: 1px;
    background-color: @border-primary;
  }
}

.news-aside__link {
  max-width: 45%;
  outline: 1px solid #cecece;
  border: 2px solid rgba(0, 0, 0, 0.1);
}

.news-aside__link img {
  display: block;
}
