.copyright {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding: 10px 20px 20px;
}

.copyright__block {
  width: 160px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.copyright__image {
  color: @lighten;
  fill: @lighten;
}

.copyright__image-social {
  color: @lighten;

  &:hover,
  &:active {
    color: @lightest;
    fill: @lightest;
  }
}
