
.describe {
  width: 120px;
  min-height: 110px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  &--tablet,
  &--desktop {
    display: none
  }

  &:hover .describe__link,
  &:active .describe__link{
    color: @accent-color;
  }
}

.describe__image {
  outline: 1px solid @border-primary;
  border: 2px solid @header-border;
  display: block;
}

.describe__title {
  font-size: 13px;
  line-height: 16px;
  font-weight: bold;
}

.describe__link {
  color: @darken;
}

.describe__add {
  font-size: 10px;
  line-height: 16px;
  margin-top: 5px;
}

@media (min-width: 420px) {
  .describe {
    width: 140px;
  }
}

@media (min-width: 500px) {
  .describe {
    width: 130px;

    &--tablet {
      display: flex;
    }
  }
}

@media (min-width: 600px) {
  .describe {
    width: 160px;
  }
}

@media (min-width: 738px) {
  .describe--desktop {
    display: flex;
  }
}

@media (min-width: 896px) {
  .describe {
    width: 180px;
  }
}

@media (min-width: 980px) {
  .describe {
    width: 140px;
  }
}

@media (min-width: 1116px) {
  .describe {
    width: 160px;
  }
}
