.page-header {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1150px;
  width: 100%;
}

.page-header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  order: -1;
}

.page-header__logo {
  min-width: 80px;
  max-width: 170px;
  display: block;


  &[href]:hover {
    filter: invert(100%);
    transition: all 0.2s ease;
  }
}

.page-header__logo-image {
  height: 100%;
  width: 100%;
}

.page-header__nav-button {
  margin-right: 2.7%;
  min-width: 24px;
  min-height: 22px;
  align-self: center;
  cursor: pointer;
  border: none;
  background-color: transparent;
  background-image: url("../../img/icons/icon-menu-close.svg");
  background-repeat: no-repeat;
  transition: all 0.2s ease-in-out;

  &--closed {
    background-image: url("../../img/icons/icon-menu-open.svg");
  }

  &:focus {
    outline: none;
  }
}
.page-header__promo {
  min-height: 40px;
}

.page-header__promo-image {
  display: block;
  min-height: 60px;
  max-height: 115px;
  width: 100%;
  border: 2px solid @border-primary;
  outline: 1px solid @darken;
}

.page-header__image {
  display: none;
}

.page-header__search {
  padding: 0 20px;
  order: -1;
}

@media @tablet {
  .page-header__logo-wrapper {
    order: 0;
  }

  .page-header__nav-button {
    display: none;
  }

  .page-header__search {
    order: 0;
  }
}

@media @tablet-large {
  .page-header__search {
    display: none;
  }

  .page-header__logo-image {
    filter: drop-shadow(2px 0 2px @lighten);
  }
}

@media @desktop {
  .page-header__image {
    display: block;
    width: 5%;
    height: auto;
    margin-left: 14px;
    filter: drop-shadow(2px 0 2px @lighten);
  }

  .page-header__promo {
    width: 800px;
  }
}
