.search__input {
  width: 100%;
  padding: 10px 40px 10px 10px;
  border: 1px solid @border-primary;

  &:focus,
  &:active,
  &:hover {
    outline: none;
    border-color: @darken;
  }

  &::placeholder {
    color: @lighten;
  }
}

.search__button {
  position: absolute;
  display: block;
  border: none;
  cursor: pointer;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-color: @white;
  background-image: url(../../img/icons/icon-search.svg);

  &:active,
  &:hover {
    opacity: 0.7;
  }

   &:focus {
    outline: none;
    background-image: url(../../img/icons/icon-search-active.svg);
   }
}

@media @tablet-large {
  .search {
    padding-bottom: 20px;
    position: relative;

    &::after {
      position: absolute;
      content: "";
      left: -20px;
      top: 100%;
      width: calc(100% + 40px);
      height: 1px;
      background-color: @border-primary;
    }
  }

  .search__input {
    padding-right: 35px;
  }
}
