.footer-additional {
  flex-direction: column;
  align-items: center;
}

.footer-additional__item {
  list-style: none;
  margin-right: 3px;

  &:last-child {
    margin-right: 0;
  }
}

.footer-additional__text,
.footer-additional__link {
  color: @lighten;
  font-size: 12px;
}

.footer-additional__link {
  &:hover,
  &:active {
    text-decoration: underline;
  }
}

@media (min-width: 465px) {
  .footer-additional {
    flex-direction: row;
  }
}
