.clause {
  padding: 20px;
  background-color: #FFFFFF;
  border: 1px solid #cecece;
  margin-bottom: 20px;
  box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.15);
  transition: 0.3s linear;
}

.clause__link {
  color: @darken;
}
.clause__title {
  text-align: center;
  margin-bottom: 20px;
}

.clause__place {
  font-size: 13px;
  font-weight: bold;
  color: @lighten;
  text-align: center;
  margin-top: -10px;
  margin-bottom: 15px;
}

.clause__img-block {
  overflow: hidden;
  outline: 1px solid #cecece;
  border: 2px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;

  &:hover .clause__img,
  &:active .clause__img {
    filter: grayscale(0);
  }
}

.clause__img {
  background: radial-gradient(50% 50% at 50% 50%, rgba(31, 31, 31, 0) 0%, rgba(31, 31, 31, 0.5) 100%);
  display: block;
  width: 100%;
  filter: grayscale(100%);
  transition: all, 0.8s;
  min-height: 175px;
}

.clause__list-icon {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}

.clause__item-icon {
  margin-right: 10%;
  min-width: 5%;

  &:last-child {
    margin-right: 0;
  }
}

.clause__icon {
  width: 100%;
  height: 100%;
  color: @border-primary;

  &--active {
    color: @default;
  }
}

.clause__text {
  color: @darkest;
  margin-bottom: 20px;
}

.clause__text-link {
  text-align: center;
  font-weight: bold;

  &:hover {
    color: @lighten;
    text-decoration: underline;
  }
}

@media @tablet-large {
  .clause:last-child {
    margin-bottom: 0;
  }
  .clause__img {
    min-height: 350px;
  }
}

@media @desktop {
  .clause__img {
    min-height: 450px;
  }
}
