.page-sidebar > *{
  display:none;
}

@media @tablet-large {
  .page-sidebar {
    margin-left: 20px;
    padding: 20px;
    background-color: #FFFFFF;
    border: 1px solid #cecece;
  }

  .page-sidebar > * {
    display: block;
  }

  .page-sidebar__noname {
    margin-top: 50px;
    padding: 20px 0;
    position: relative;

    &::before,
    &::after {
      position: absolute;
      content: "";
      left: -20px;
      top: 0;
      width: calc(100% + 40px);
      height: 1px;
      background-color: @border-primary;
    }

    &::after {
      top: 100%;
    }
  }
}
