.button-scroll {
  position: fixed;
  z-index: 1;
  padding: 0;
  bottom: 125px;
  right: 20px;
  width: 34px;
  height: 20px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  background-color: @lighten;
  opacity: 0.5;
  color: @lightest;
  border: 2px solid @lighten;
  border-right-width: 0;
  display: none;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    color: @darkest;
  }

  &:focus {
    outline: none;
    border: 1px solid @default;
    border-right: none;
  }

  &--up {
    border-top-left-radius: 5px;
  }

  &--down {
    border-bottom-left-radius: 5px;
    bottom: 104px;
  }
}

  .shown {
    display: inline-block;
  }


@media @tablet {
  .button-scroll {
    bottom: 105px;

    &--down {
        bottom: 84px;
    }
  }
}
