.top-nav {
  margin: 0;
  padding: 0;
  order: 1;
}

.top-nav__list {
  list-style: none;

  &--contacts {
    display: none;
  }
}

.top-nav__item {
  border-bottom: 1px solid @border-primary;
}

.top-nav__link {
  text-decoration: none;
  color: @default;
  line-height: 20px;
  font-weight: bold;
  display: block;
  padding: 16px 33px;

  &:hover[href] {
    color: @lighten;
  }

  &:active[href] {
    color: @lightest;
    background-color: @darken;
  }
}


@media @tablet {
  .top-nav {
    order: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: @darken;
    padding: 0 10px;
    position: relative;
  }

  .top-nav__list {
    display: flex;
    flex-wrap: wrap;
  }

  .top-nav__item {
    border-bottom: none;

    &:hover > .submenu-top {
      left: 2.5%;
    }

    &:hover .top-nav__link {
      color: @lightest;
    }
  }

  .top-nav__link {
    font-size: 12px;
    color: @lighten;
    padding: 11px 1vw;
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      height: 20px;
      width: 1px;
      background-color: @default;
      box-shadow: 1px 0 1px 0 @darkest;
      left: 0;
    }

    &::after {
      left: 100%;
    }

    &--first::before,
    &--last::after {
      content: none;
    }

    &:active[href] {
      opacity: 0.8;
    }
  }

  .top-nav__icon {
    margin-right: 7px;

    &::before {
      content: "\260E";
    }

    &--cooperation {
      &::before {
        content: "\2714";
      }
    }
  }
}
