.submenu {
  list-style: none;
  width: 100%;
  height: 100%;
}

.submenu__item {
  // background-image: linear-gradient(to bottom, @border-primary 30%, @lighten 100%);
  background-color: @bg-lighten;
  border-bottom: 1px solid @lightest;

  &:first-child {
    border-top: 1px solid @lightest;
  }
}

.submenu__link {
  text-decoration: none;
  color: @default;
  line-height: 20px;
  font-weight: bold;
  font-size: 13px;
  display: block;
  padding: 14px 22px 14px 54px;

  &:hover[href] {
    color: @black;
  }

  &:active[href] {
    color: @lightest;
    background-color: @darken;
  }

  &--active {
    color: @lightest;
    background-color: @default;
  }
}

