@media @tablet {
  .submenu {
    position: absolute;
    top: 98%;
    left: 2px;
    min-width: 150px;
    width: auto;
    height: auto;
    box-shadow: 0 2px 3px 1px rgba(21, 21, 21, 0.6);
    transition: 0.5s transform;
    z-index: 11;
  }

  .submenu__item {
    background-color: @grey--darken;
    border: none;
    position: relative;

    &:first-child {
      border: none;
    }

    &::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 83%;
    background-color: @darkest;
    box-shadow: 0 1px 3px 0 white;
    left: 6%;
    bottom: 0;
    }

    &:last-child::after {
      display: none;
    }
  }

  .submenu__link {
    padding: 8px 16px;
    color: @lighten;
    font-size: @font-size-base;

    &:hover[href] {
      color: @color-lighten;
    }
  }

  .submenu__button,
  .submenu__item--main {
    display: none;
  }
}

@media @tablet-large {
  .submenu {
    min-width: 200px;
    box-shadow: none;
    z-index: 1;
    top: calc(100% - 4px);

    &::before {
      content: "";
      position: absolute;
      height: 3px;
      width: 100%;
      background-color: rgba(31, 32, 32, 0.1);
      left: 0;
      top: 0;
      z-index: 2;
      box-shadow: 0px 3px 4px 0 rgba(0, 0, 0, 0.45);
    }
  }

  .submenu__item {
    box-shadow: 0 3px 4px 1px rgba(21, 21, 21, 0.5);

    &:first-child .submenu__link {
      padding-top: 12px;
    }

    &:last-child {
      border-radius: 0 0 5px 5px;
    }
  }
}

@media @desktop {
  .submenu {
    display: none;
  }

  .submenu__link {
    padding: 8px 20px;
  }

  .submenu__item {
    &::after {
      width: 80%;
      left: 10%;
    }
  }
}
