.page-footer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 20px 20px 0;
  max-width: 1150px;
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  color: @lighten;
  font-size: 13px;
  line-height: 16px;
  font-weight: bold;
}

.page-footer__header {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: @darkest;
}

.page-footer__header-link {
  color: @lighten;

  &:hover,
  &:active {
    color: @lightest;
  }
}

.page-footer__wrapper {
  background-color: @darken;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid @border-action;
}

.page-footer__copyright {
  background-color: @darken;
  border-top: 1px solid @border-action;
}
