.submenu-top {
  display: none;
}

@media @tablet-large {
  .submenu-top {
    list-style: none;
    padding: 10px;
    width: 95%;
    display: flex;
    flex-wrap: wrap;
    background-color: @lightest;
    opacity: 0.9;
    position: absolute;
    min-height: 50px;
    z-index: 1;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    box-shadow: 0 2px 3px 1px rgba(21, 21, 21, 0.6);
    transition: all 0.5s ease;
    left: -100vw;

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      background-color: @darken;
      left: 0;
      top: 0;
      z-index: 2;
      box-shadow: 0px 1px 4px 2px rgba(0, 0, 0, 0.45);
    }
  }

  .submenu-top__item {
    display: flex;
    padding: 10px;
    min-width: 11%;
  }

  .submenu-top__link {
    font-size: 12px;
    line-height: 20px;
    font-weight: bold;
    color: @default;
    text-shadow: 1px 1px 5px @lighten;
    margin: 0 auto;

    &:hover[href],
    &:active[href] {
      color: @accent-color;
    }

    &--active {
      opacity: 0.5;
      text-shadow: 1px 1px 5px @darkest;
    }
  }
}
