.page {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
}

@media @desktop {
  .page::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 43px;
    background-color: @darken;
    z-index: -1;
  }
}
