.video {
  display: block;
  height: 170px;
  padding: 0 0 20px;
}

.video__title {
  color: @darken;
  font-size: 15px;
  margin: 20px 0;
}

.video__play {
  width: 100%;
  height: 100%;
  background-color: lightgrey;
}

@media (min-width: 420px) {
  .video {
    height: 225px;
  }
}

@media (min-width: 520px) {
  .video {
    height: 280px;
  }
}

@media (min-width: 620px) {
  .video {
    height: 335px;
  }
}

@media @tablet-large {
  .video {
    height: 160px;
    margin-bottom: 50px;
  }
}
