html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  background-color: @bg-body;
  font-size: 15px;
  line-height: 24px;
  color: @default;
  min-width: @mobile-width;
}

a {
  text-decoration: none;
}

p,h1,h2,h3,figure,ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

