.footer-nav {
  padding: 20px 20px 30px;
}

.footer-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer-nav__item {
  list-style: none;
  width: 40%;
  padding-top: 10px;
  border-bottom: 1px solid @border-action;

  &:nth-child(2n) {
    width: 47%;
  }
}

.footer-nav__link {
  color: @lightest;
  line-height: 20px;
  font-weight: normal;

  &:hover[href],
  &:active[href] {
    color: @lighten;
  }
}

@media @tablet-large {
  .footer-nav {
    min-width: 64%;
    width: 64%;
  }

  .footer-nav__item:nth-child(n) {
    width: 47%;
  }
}
