.theme {
  padding: 20px;
  background-color: @white;
  border: 1px solid @border-primary;
  margin-bottom: 20px;
  box-shadow: 0 6px 8px 0 @action--lightly;
  transition: .3s linear;

  &:hover,
  &:active {
    box-shadow: 6px 6px 8px 0 @action--lightly;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.theme__zoom {
  overflow: hidden;
  outline: 1px solid @border-primary;
  border: 2px solid @header-border;
}

.theme__image {
  display: block;
  min-width: 240px;
  width: 100%;
  transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  transform-style: flat;

  &:hover,
  &:active {
    transform: scale(1.2);
  }
}

.theme__link-title {
  text-decoration-color: @darken;

  &:hover,
  &:active {
    text-decoration: underline;
    text-decoration-color: @darken;
  }
}

.theme__title {
  margin: 20px 0 15px;
  color: @darken;
}

.theme__add {
  margin-bottom: 15px;
  font-size: 12px;
  color: @lighten;
}

.theme__text {
  line-height: 20px;
}

@media @tablet {
  .theme {
    width: 48.5%;

    &:last-child {
      margin-bottom: 20px;
    }
  }
}
