.page-main {
  padding: 20px;
  max-width: 1150px;
  margin: 0 auto 20px;
  flex-grow: 1;
}

.page-main__title {
  font-size: 26px;
  margin-bottom: 10px;
}

.page-main__text {
  font-size: 18px;
  margin-bottom: 20px;
}

@media @tablet-large {
  .page-main {
    display: flex;
  }

  .page-main__content {
    width: 70%;
    min-width: 70%;

    &--history {
      width: 100%;
      min-width: 100%;
    }
  }
}
